body{
  background-image: url(/todo_app_frontend/static/media/green-chameleon-21532-unsplash.9bbbb4ae.jpg);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

.my-navbar{
  background-color: midnightblue;
}



.myRow td,
.myRow td button {
    -webkit-transition: all 0.5s ease;  
    transition: all 0.5s ease;
}

.myRowAfterButtonClick,
.myRowAfterButtonClick td,
.myRowAfterButtonClick td button {
    font-size: 0px;
    margin: 0px;
    border: 0px;
    padding: 0px;
}
